.main-container {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    height: 100vh;
    background-color: #f0f4fa;
}

.weather {
    height: 80vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 30px;
    border-radius: 20px;
    background-image: linear-gradient(135deg, #2f4680, #500ae4);
    box-shadow: 0 8px 30px rgba(0, 0, 0, 0.2);
    width: 90%; /* Adjust width for smaller screens */
    max-width: 400px; /* Limit maximum width */
    text-align: center;
}

.search-bar {
    display: flex;
    align-items: center;
    gap: 12px;
    width: 100%;
    margin-bottom: 20px;
}

.search-bar input {
    height: 40px;
    width: calc(100% - 60px);
    padding-left: 15px;
    outline: none;
    border: 2px solid #fff;
    border-radius: 20px;
    font-size: 1rem;
    color: #333;
    background: #ffffff;
    transition: border-color 0.3s ease;
}

.search-bar input:focus {
    border-color: #76c7c0;
}

.search-bar img {
    width: 40px;
    height: 40px;
    padding: 8px;
    border-radius: 50%;
    cursor: pointer;
    background: #ffffff;
    transition: background 0.3s ease, transform 0.2s ease;
}

.search-bar img:hover {
    background: #76c7c0;
    transform: scale(1.1);
}

.weather-icon {
    width: 100px;
    margin: 20px 0;
    filter: drop-shadow(0 4px 10px rgba(0, 0, 0, 0.2));
}

.temp {
    color: #ffffff;
    font-size: 60px;
    line-height: 1;
    font-weight: bold;
    text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.4);
}

.location {
    color: #ffffff;
    font-size: 24px;
    margin-top: -5px;
    font-weight: 500;
}

.weather-data {
    margin-top: 20px;
    width: 100%;
    color: #ffffff;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap; /* Allow wrapping for smaller screens */
    gap: 10px; /* Space between items when wrapping */
}

.weather-data .col {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 5px;
    font-size: 18px;
}

.weather-data .col span {
    display: block;
    font-size: 14px;
    color: #d4d4d4;
}

.weather-data .col img {
    width: 30px;
    margin-bottom: 5px;
    filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.3));
}

.weather-data .col p {
    font-weight: bold;
    color: #ffffff;
}

/* Responsive Styles */
@media (max-width: 768px) {
    .temp {
        font-size: 50px;
    }

    .location {
        font-size: 20px;
    }

    .weather-data .col {
        font-size: 16px;
    }

    .weather-data .col img {
        width: 25px;
    }
}

@media (max-width: 480px) {
    .search-bar input {
        font-size: 0.9rem;
        width: calc(100% - 50px);
    }

    .search-bar img {
        width: 35px;
        height: 35px;
    }

    .weather {
        padding: 20px;
    }

    .temp {
        font-size: 40px;
    }

    .location {
        font-size: 18px;
    }

    .weather-data {
        gap: 5px;
    }

    .weather-data .col {
        font-size: 14px;
    }

    .weather-data .col img {
        width: 20px;
    }
}
